.nav {
  width: 100%;
  height: 100px;
  background-color: var(--background-color);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  font-family: "Fira sans", sans-serif;
  z-index: 999;
  color: var(--text-color);
}

.topLeft,
.topRight {
  flex: 3 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.topCenter {
  flex: 6 1;
}
.topLeft img {
  height: auto;
  max-width: 100%;
  margin-top: -4rem;
  cursor: pointer;
}
.topImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}
.topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.topListItem {
  margin-right: 1.2rem;
  font-size: 1.2rem;
  font-weight: 100;
  cursor: pointer;
}
.topSearchIcon {
  font-size: 1.2rem;
  color: gray;
  cursor: pointer;
  margin-left: 1rem;
}
.defaultImg {
  font-size: 1.4rem;
}

#darkMode {
  margin-left: 1.5rem;
  background: transparent url(/static/media/moon.08c8560a.png) no-repeat center;
  background-size: 30px 30px;
  width: 38px;
  height: 38px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  border: none;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
}

#darkMode.clicked {
  -webkit-filter: none !important;
          filter: none !important;
  background-color: black;
}

.home {
