.posts {
  flex: 9 1;
  display: flex;
  flex-wrap: wrap;
  margin: 1rem;
}
.paginationBtn {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
}
.paginationBtn a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid gray;
  color: var(--text-color);
  cursor: pointer;
  background-color: var(--background-color);
}
.paginationBtn a:hover {
  background-color: darkgray;
}
.paginationActive a {
  background-color: rgb(233, 219, 219);
}
.noPosts {
  display: flex;
  align-items: center;
  justify-content: center;
}
.noPostsPara {
  margin: 2rem;
  font-size: 1.6rem;
  width: 70%;
  text-align: center;
}

.post {
  width: 375px;
  margin: 0 1rem 2rem 1.2rem;
}
.postImg {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
}
.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.postTitle {
  font-family: "Fira Sans", sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  margin-top: 0.8rem;
  cursor: pointer;
}
.postDate {
  font-family: "Lora", sans-serif;
  font-style: italic;
  font-size: 1rem;
  color: var(--text-color);
  margin-top: 0.8rem;
}
.postDesc {
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem;
  color: var(--text-color);
  line-height: 1.2rem;
  margin-top: 0.8rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.main-loader {
  display: flex;
  align-items: center;
}

.bar {
  width: 10px;
  height: 70px;
  background: #fff;
  display: inline-block;
  transform-origin: bottom center;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  /*   box-shadow:5px 10px 20px inset rgba(255,23,25.2); */
  animation: loader 1.2s linear infinite;
}
.bar1 {
  animation-delay: 0.1s;
}
.bar2 {
  animation-delay: 0.2s;
}
.bar3 {
  animation-delay: 0.3s;
}
.bar4 {
  animation-delay: 0.4s;
}
.bar5 {
  animation-delay: 0.5s;
}
.bar6 {
  animation-delay: 0.6s;
}
.bar7 {
  animation-delay: 0.7s;
}
.bar8 {
  animation-delay: 0.8s;
}

@keyframes loader {
  0% {
    transform: scaleY(0.1);
  }
  50% {
    transform: scaleY(1);
    background: rgb(226, 197, 186);
  }
  100% {
    transform: scaleY(0.1);
    background: transparent;
  }
}

/*Spinner*/
.loadingWrapper {
  position: relative;
}
.lds-ripple {
  position: absolute;
  transform: translate(310%, 150%);
  width: 200px;
  height: 200px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid rgb(194, 178, 178);
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

