:root {
  --black: #000;
  --white: #fff;
  --gray: gray;
}
body {
  background: var(--background-color);
  color: var(--text-color);
}
body.light {
  --background-color: var(--white);
  --text-color: var(--black);
}
body.dark {
  --background-color: var(--black);
  --text-color: var(--gray);
}

